/* Style the green strip header */
.green-strip {
  background-color: #1CB25F;
  color: black;
  text-align: center;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

/* Style the content */
.content {
  text-align: center;
  padding: 50px;
}
.green-strip p {
  width: 200px;
  text-align: left;
}
.content img {
  max-width: 80%; /* Make sure the image doesn't exceed its container's width */
}
